@import url("https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Nunito+Sans:ital,opsz,wght@0,6..12,200..1000;1,6..12,200..1000&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --dark: #111111;
  --white: #ffffff;
  --primary: #475fcc;
  --secondary: #0e0e0e;
  --tertiary: #9e9e9e;
  --danger: #cf4b4b;
  --success: #4bb64b;
  --warning: rgb(212, 190, 64);
  --main: #f3f2ee;
  --main-dark: #e4e3de;
  --overlay-1: rgba(0, 0, 0, 0.3);
  --overlay-2: rgba(0, 0, 0, 0.5);
  --gray: #f1f1f16c;
  --disabled-1: #111111ab;
  --disabled-2: #cf4b4bbd;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}

body {
  font-family: "Nunito Sans", sans-serif;
  position: relative;
}

section {
  width: 100%;
  padding-block: 70px;
}

section:last-of-type{
  padding: 0;
}

section:first-of-type {
  padding-top: 100px;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  display: inline-block;
}

button {
  background: none;
  border: none;
}

.hidden {
  overflow: hidden;
}

.resize-animation-stopper * {
  transition: none !important;
  animation: none !important;
  scroll-behavior: unset !important;
}

.container {
  padding-inline: 16px;
  margin-inline: auto;
  width: 1184px;
}

.brand {
  font-size: 32px;
}

.success{
  color: var(--success);
}

.failed{
  color: var(--danger);
}

.heading-1 {
  font-size: 48px;
  margin-bottom: 30px;
  line-height: 58px;
}

.heading-2 {
  font-size: 24px;
  margin-bottom: 30px;
  line-height: 30px;
  position: relative;
}

.heading-3 {
  color: var(--danger);
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 15px;
  font-weight: 700;
}

.heading-4 {
  color: var(--dark);
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 800;
}

.heading-5 {
  color: var(--danger);
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 700;
  font-size: 20px;
  margin-bottom: 40px;
}

.body-text-1 {
  font-size: 14px;
  color: var(--secondary);
  line-height: 28px;
  letter-spacing: 1px;
  margin-bottom: 30px;
}

.body-text-2 {
  font-size: 14px;
  color: var(--tertiary);
  line-height: 28px;
  letter-spacing: 1px;
  margin-bottom: 14px;
}

.body-text-3 {
  font-size: 14px;
  color: var(--secondary);
  letter-spacing: 1px;
}

.button-1 {
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 14px 30px;
  color: var(--white);
  background: var(--dark);
  letter-spacing: 3px;
  border: none;
  cursor: pointer;
}

.button-1:disabled{
  background-color: var(--disabled-1);
}

.button-2 {
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
  padding: 14px 30px;
  color: var(--white);
  background: var(--danger);
  letter-spacing: 4px;
  border: none;
  cursor: pointer;
}

.button-2:disabled{
  background-color: var(--disabled-2);
}

.button-3 {
  font-size: 13px;
  font-weight: 500;
  text-transform: uppercase;
  padding: 4px 8px;
  color: var(--white);
  background: var(--success);
  border: none;
  cursor: pointer;
}

.fa-spin{
  animation: spin 1s linear infinite;
}

.ellipses {
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  line-clamp: 1;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.ellipses-2 {
  text-overflow: ellipsis;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.pg-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--overlay-2);
  z-index: 998;
}

@keyframes spin {
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}

/* --------------------loader-------------------- */


#loader > .container{
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

.loader {
  width: 50px;
  padding: 8px;
  aspect-ratio: 1;
  border-radius: 50%;
  background: var(--dark);
  --_m: 
    conic-gradient(#0000 10%,#000),
    linear-gradient(#000 0 0) content-box;
  -webkit-mask: var(--_m);
          mask: var(--_m);
  -webkit-mask-composite: source-out;
          mask-composite: subtract;
  animation: l3 1s infinite linear;
}

@keyframes l3 {to{transform: rotate(1turn)}}

/* --------------------Pagination-------------------- */

#pagination {
  padding-top: 0;
}

.pagination-container {
  display: flex;
  justify-content: center;
  gap: 4px;
  margin-inline: auto;
}

.pagination-container > button {
  width: 35px;
  height: 35px;
  border: 1px solid var(--dark);
  cursor: pointer;
}

.pagination-btn-active {
  background-color: var(--dark);
  color: var(--white);
}

/* --------------------Rating-------------------- */

.rating-container {
  display: flex;
}

.active-star {
  color: gold;
}

.inactive-star {
  stroke: gold;
  stroke-width: 50px;
  fill: white;
  width: 14.5px;
}

/* --------------------Navbar-------------------- */

nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--white);
  box-shadow: 1px 1px 10px var(--tertiary);
  z-index: 997;
}

nav > .container {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: var(--white);
}

.nav-active {
  box-shadow: 1px 1px 10px var(--tertiary);
}

.nav-brand {
  margin-right: 24px;
  padding-block: 5px;
  color: var(--dark);
}

.nav-brand > span {
  color: var(--danger);
}

.navbar-collapse {
  flex: 1;
  display: flex;
  align-items: center;
}

.nav-page-links {
  display: flex;
  align-items: center;
}

.nav-user-links {
  display: flex;
  align-items: center;
  margin-left: 24px;
}

.nav-page-links > li,
.nav-user-links > li {
  margin: 8px;
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
}

.nav-page-links > li > a,
.nav-user-links > li > a {
  color: var(--dark);
  position: relative;
  padding-block: 5px;
  display: flex;
  align-items: center;
  overflow: hidden;
  font-size: 14px;
  text-transform: uppercase;
}

.nav-page-links > li > a::after {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: var(--danger);
  transform: translateX(-101%);
  transition: transform 0.3s ease-in-out;
}

.nav-user-links > li svg {
  width: 20px;
  height: 20px;
}

.nav-page-links > li > a:hover::after {
  transform: translateX(0);
}

.link-active::before {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: var(--danger);
}

.toggle-collapse {
  align-items: center;
  display: none;
  margin-right: auto;
  cursor: pointer;
}

.toggle-collapse > svg {
  height: 24px;
  width: 24px;
}

.badge {
  position: absolute;
  right: -15px;
  top: -15px;
  width: 24px;
  height: 24px;
  background-color: var(--danger);
  color: var(--main);
  text-align: center;
  border-radius: 50%;
  font-size: 12px;
  padding: 4px;
}

.nav-profile-link {
  position: relative;
}

.nav-profile-badge {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid var(--dark);
  overflow: hidden;
  cursor: pointer;
}

.nav-profile-badge > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
}

.nav-profile-options {
  position: absolute;
  top: 20px;
  right: -8px;
  background-color: transparent;
  display: flex;
  flex-direction: column;
  gap: 10px;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  padding-top: 28px;
}

.nav-profile-options > li {
  display: flex;
  justify-content: center;
  align-items: center;
  text-transform: capitalize;
  gap: 4px;
  color: var(--dark);
  cursor: pointer;
  background-color: var(--white);
  box-shadow: 1px 1px 10px var(--overlay-1);
  border-radius: 50%;
  padding: 8px;
}


.nav-profile-options > li:hover {
  background-color: var(--main);
}

.nav-profile-options > li > svg{
  height: 18px !important;
  width: 18px !important;
}


.nav-profile-options-active {
  visibility: visible;
  opacity: 1;
}

/* --------------------Hero-------------------- */

#hero {
  background-color: var(--main);
  background-size: cover;
  background-position:center center;
  height: 65vh;
}

#hero > .container {
  display: grid;
  align-items: center;
  grid-template-columns: 6fr 6fr;
  height: 100%;
}

/* --------------------Trending Products-------------------- */

.trending-products-container {
  overflow-x: hidden;
  padding-block: 8px;
  scroll-behavior: smooth;
  width: 100%;
}

.slider-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.slider-buttons {
  display: flex;
  gap: 8px;
}

.slider-buttons > button {
  background-color: var(--dark);
  padding: 8px;
  cursor: pointer;
}

.slider-buttons > button > svg {
  margin-block: auto;
  color: var(--white);
  font-size: 20px;
}

.slider {
  display: flex;
  width: fit-content;
  gap: 16px;
  padding-inline: 4px;
}

.trending-products-container::-webkit-scrollbar {
  height: 0;
}

.slider > .product-card {
  width: 276px;
}

/* --------------------Trending Category-------------------- */

#trending-category > .container {
  height: 550px;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  grid-template-columns: repeat(4, 1fr);
  grid-template-areas:
    "a a b c"
    "a a b d";
  gap: 8px;
}

.category {
  position: relative;
  overflow: hidden;
  cursor: pointer;
}

.category-overlay {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: var(--overlay-1);
  transition: background-color 0.3s ease-in-out;
}

.category:hover > .category-overlay {
  background-color: var(--overlay-2);
}

.category:nth-child(1) {
  grid-area: a;
}
.category:nth-child(2) {
  grid-area: b;
}
.category:nth-child(3) {
  grid-area: c;
}
.category:nth-child(4) {
  grid-area: d;
}

.category-overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: var(--overlay-1);
}

.category > h2 {
  color: var(--white);
  position: absolute;
  bottom: 8px;
  left: 8px;
  text-transform: capitalize;
}

.category > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: 50% 50%;
}

/* --------------------Best Seller-------------------- */

#promotion-banner {
  background-color: var(--main);
  background-size: cover;
  background-position: 30% 85%;
  background-repeat: no-repeat;
  height: 50vh;
}

#promotion-banner > .container {
  display: grid;
  grid-template-columns: 6fr 6fr;
  align-items: center;
}


.promotion-banner-image > img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center center;
}
/* --------------------Best Seller-------------------- */

#bestseller > .container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.bestseller-container {
  width: 100%;
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: repeat(4, 1fr);
  gap: 16px;
}

/* --------------------Product Card-------------------- */

.product-card {
  cursor: pointer;
  position: relative;
  background-color: var(--white);
  background-color: transparent;
  box-shadow: 1px 1px 10px -3px var(--tertiary);
  transition: all 0.3s ease-in-out;
}


.product-card:hover{
  box-shadow: 1px 1px 10px 0px var(--tertiary);
} 

.product-card:hover > .product-card-content >.product-card-options{
  transform: translateY(0);
}

.product-card-img{
  background-color: var(--gray);
}

.product-card-img img {
  width: 100%;
  height: 260px;
  object-fit: cover;
  background-position: center center;
  mix-blend-mode: multiply;
}

.product-card-content {
  display: flex;
  flex-direction: column;
  padding: 8px;
  position: relative;
  overflow: hidden;
}

.product-card-content > h5 {
  font-size: 14px;
  margin-bottom: 8px;
}

.product-card-content > h4 {
  font-size: 15px;
  font-weight: 700;
  margin-top: 16px;
}

.product-card-options {
  position: absolute;
  bottom: 0;
  right: 0;
  transform: translateY(100%);
  transition: transform 0.3s ease-in-out;
}

.product-card-options > svg {
  height: 20px;
  width: 20px;
  margin: 8px;
}

/* --------------------Product Page-------------------- */

#product-content > .container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.product-content-images {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--gray);
}

.product-content-images img {
  width: 100%;
  mix-blend-mode: multiply;
}

.product-content-description {
  padding: 32px;
  display: flex;
  flex-direction: column;
  /* background-color: red; */
}

.product-content-description h1 {
  margin-bottom: 10px;
  align-self: flex-start;
}
.product-content-rating {
  display: flex;
  margin-bottom: 20px;
}

.product-content-rating > h6 {
  color: var(--tertiary);
  font-size: 14px;
  padding-left: 8px;
  margin-left: 8px;
  border-left: 2px solid var(--tertiary);
}

.description > h3 {
  color: var(--dark);
}

.quantity {
  display: flex;
  align-items: center;
  margin-bottom: 16px;
}

.quantity-container {
  display: flex;
  align-items: center;
  background-color: var(--main);
  margin-right: 16px;
}
.quantity-container > button,
.quantity-container span {
  padding: 8px 16px;
  font-size: 16px;
}

.quantity-container > span {
  width: 55px;
  text-align: center;
}

.quantity-container > button {
  cursor: pointer;
}

.stock-message {
  color: var(--danger);
  text-transform: capitalize;
  font-size: 18px;
}

[data-stock="false"] {
  color: var(--success);
}

.product-content-price {
  margin-bottom: 16px;
}

.product-content-btns {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
}

.product-reviews{
  display: grid;
  grid-template-columns: 4fr 8fr;
  gap: 16px;
}

.overall-rating{
  display: flex;
  flex-direction: column;
  padding: 16px;
  box-shadow: 1px 1px 10px -3px var(--tertiary);
  height: fit-content;
}

.overall-rating > h4{
  margin-block: 8px;
  font-size: 14px;
  color: var(--dark);
}
.overall-rating > p{
  margin-block: 8px;
  font-size: 14px;
  color: var(--secondary);
  letter-spacing: 1px;
}

.reviews-container{
  display: flex;
  flex-direction: column;
}

.review-form{
  margin-bottom: 16px;
}

.review-form > input[type='text']{
  margin-top: 10px;
  width: 100%;
  padding: 10px 16px;
  outline: none;
  color: var(--dark);
  border: 1px solid var(--tertiary);
  border-radius: 5px;
}

.review-form > input[type='submit']{
  margin-top: 16px;
}

.user-reviews{
  display: flex;
  flex-direction: column;
  gap: 16px;
  margin-top: 16px;
}

.review{
  background-color: var(--main);
  padding: 16px;
}

.review-head > h1{
  font-size: 16px;
  color: var(--dark);
}

.review-head > div{
  display: flex;
  align-items: center;
  gap: 8px;
}

.review-head > div > p{
  font-size: 16x;
  font-weight: 600;
  letter-spacing: 1px;
}

.comment{
  margin-block: 10px;
  font-size: 14px;
  color: var(--secondary);
  letter-spacing: 1px;
}

/* --------------------Browse-------------------- */


#browse-filters {
  padding-bottom: 0;
}

.browse-container {
  background-color: var(--main);
  padding: 16px;
}

.category-container {
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin-bottom: 16px;
}

.category-container > span {
  border: 1px solid var(--tertiary);
  color: var(--tertiary);
  padding: 4px 16px;
  cursor: pointer;
  font-size: 14px;
}

.active-category {
  border-color: var(--dark) !important;
  color: var(--dark) !important;
}

#browse-results > .container {
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: repeat(4, 1fr);
  gap: 16px;
}

#pagination > .container{
  height: 20vh;
}

/* --------------------Login-------------------- */

#auth-container {
  background-color: var(--main);
  height: 95vh;
}

#auth-container > .container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--white);
  width: fit-content;
  min-width: 400px;
  padding: 32px 16px;
  box-shadow: 1px 1px 10px -3px var(--tertiary);
  border-radius: 4px;
}

.form-container > h1 {
  padding-bottom: 0;
  margin-bottom: 0;
  text-transform: uppercase;
  font-weight: 800;
}

.form-container > p {
  text-align: center;
}

.form-container > form {
  display: flex;
  flex-direction: column;
  width: 100%;
  gap: 16px;
}

.form-label {
  display: flex;
  flex-direction: column;
  position: relative;
}

.form-label > input, .form-label > textarea {
  font-size: 14px;
  padding: 10px 16px;
  border: 1px solid var(--tertiary);
  outline: none;
  border-radius: 4px;
  width: 100%;
}

.form-label > input[type='file'] + span{
  top: -6px;
  left: 10px;
  font-size: 12px;
  padding-inline: 4px;
}

.form-label > input[type='file']{
  padding-block: 16px;
}

.form-label > input:user-valid + span,
.form-label > input:user-invalid + span,
.form-label > input:focus + span,
.form-label > input:user-valid + span,
.form-label > textarea:user-valid + span,
.form-label > textarea:user-invalid + span,
.form-label > textarea:focus + span,
.form-label > textarea:user-valid + span {
  top: -6px;
  left: 10px;
  font-size: 12px;
  padding-inline: 4px;
}

.form-label > input:user-invalid + span {
  color: var(--danger);
}

.form-label > input:focus {
  border-color: var(--dark);
}

.form-label > span {
  position: absolute;
  top: 14px;
  left: 16px;
  color: var(--dark);
  background-color: var(--white);
  font-weight: 600;
  transition: all 0.3s ease-in-out;
  line-height: 14px;
  text-transform: capitalize;
}

.form-container > form > button{
  border-radius: 4px;
}

.form-link {
  color: var(--primary);
  align-self: flex-end;
  text-transform: capitalize;
  font-size: 14px;
}

.form-link:hover {
  text-decoration: underline;
}

.form-container > form > p:last-of-type {
  font-size: 14px;
}

.form-error-msg {
  font-size: 14px;
  font-weight: 500;
  padding-left: 2px;
  color: var(--danger);
}

/* --------------------Sign Up-------------------- */

.signup-form {
  display: grid !important;
  grid-template-rows: auto;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
    "a b"
    "c c"
    "d d"
    "e e"
    "f f"
    "g ."
    "h h"
    'i i';
    gap: 16px;
}

.signup-form > label:nth-child(1) {
  grid-area: a;
}
.signup-form > label:nth-child(2) {
  grid-area: b;
}
.signup-form > label:nth-child(3) {
  grid-area: c;
}
.signup-form > label:nth-child(4) {
  grid-area: d;
}
.signup-form > label:nth-child(5) {
  grid-area: e;
}
.signup-form > label:nth-child(6) {
  grid-area: f;
}
.signup-form > button {
  grid-area: h;
}
.signup-form > p {
  grid-area: i;
}

.profile-preview-img{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;
  border: 1px solid var(--dark);
}

.profile-preview-img > img{
  width: 100%;
