/* --------------------Cart-------------------- */

.cart-container {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 999;
  display: flex;
  flex-direction: column;
  width: 400px;
  height: 100%;
  background-color: var(--white);
  transform: translateX(100%);
  transition: all 0.3s ease-in-out;
}

.cart-active {
  transform: translateX(0);
}

.cart-header {
  height: 60px;
  background-color: var(--dark);
  display: flex;
  align-items: center;
  padding-inline: 16px;
}

