  height: 100%;
  padding: 16px;
}
.cart-items {
  height: 100%;
  margin-bottom: 16px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.cart-item {
  display: grid;
  border: 1px solid var(--dark);
