  justify-content: space-between;
  position: relative;
  overflow: hidden;
}

.progress {
  position: absolute;
  top: 50%;
  left: 0;
  background-color: var(--success);
  max-width: calc(100% - 72px) !important;
  height: 4px;
  z-index: -1;
  margin-inline: 36px;
  transition: width 0.3s ease-in-out;
