  grid-template-columns: repeat(4, 1fr);
  gap: 16px;
}

.wishlist-card-options{
  margin-top: 8px;
  display: flex;
  justify-content: flex-end;
  gap: 5px;
}

.wishlist-card-options > button{
  color: var(--tertiary);
  font-size: 12px;
  cursor: pointer;
}

.wishlist-card-options > button:hover{
  text-decoration: underline;
}

/* --------------------Footer-------------------- */

footer {
  padding-top: 70px;
  position: relative;
  background-color: var(--dark);
}

.footer-brand {
  color: var(--white);
  margin-bottom: 24px;
}

.footer-brand > span {
  color: var(--danger);
}

.footer-content {
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: 4fr 12fr;
}

.footer-content > div:first-of-type {
  padding-right: 24px;
}

.footer-content > div:first-of-type > p {
  margin-bottom: 24px;
}

.footer-content > div:first-of-type > ul {
  display: flex;
  margin-bottom: 16px;
}

.footer-list-icons > li{
  padding-right: 8px;
}
.footer-list-icons > li > a > svg{
  width: 16px;
  height: 16px;
}

.footer-content > div:last-of-type {
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: repeat(4, 1fr);
  padding-left: 24px;
}

.footer-content > div:last-of-type > ul {
  padding-right: 16px;
  margin-top: 56px;
}

.footer-content > div:last-of-type > ul:nth-child(4) {
  padding-right: 0;
}

.footer-content > div > ul > li > a {
  margin-block: 4px;
  color: var(--tertiary);
  font-size: 14px;
}

.footer-copyright {
  padding-block: 20px;
  border-top: 1px solid var(--secondary);
  margin-top: 32px;
}

.footer-copyright > p {
  text-align: center;
  color: var(--tertiary);
  font-size: 14px;
}

.footer-copyright > p > a {
  color: var(--danger);
}

/* --------------------Not Found-------------------- */

#not-found{
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

#not-found > .container{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

#not-found > .container > svg{
  height: 100px;
  width: 100px;
  margin-bottom: 16px;
}


/* --------------------Skeleton-------------------- */

.skeleton{
  background-image: linear-gradient(90deg, #ccc 0px, rgba(229, 229, 229, 0.9) 40px, #ccc 80px);
  background-size: 300%;
  background-position: 100% 0;
  border-radius: inherit;
  animation: shimmer 1.5s linear infinite;
}

.category-skeleton{
  width: 100px;
  height: 30px;
}

.product-card-skeleton{
width: 100%;
height: 355px;
}

.product-card-skeleton-2{
width: 270px;
height: 355px;
}

.product-image-skeleton{
  width: 100%;
  aspect-ratio: 1;
}

.list-item-skeleton{
  width: 100%;
  height: 65px;
  margin-bottom: 4px;
  border-radius: 5px;
}

.title-skeleton{
  width: 100%;
  height: 30px;
  margin-bottom: 16px;
}

.title-2-skeleton{
  width: 100px;
  height: 30px;
  margin-bottom: 16px;
}

.rating-skeleton{
  width: 100px;
  height: 15px;
  margin-bottom: 16px;
}

.product-rating-skeleton{
  width: 50%;
  height: 15px;
  margin-bottom: 16px;
}

.description-skeleton{
  width: 100%;
  height: 200px;
  margin-bottom: 16px;
}

.product-quantity-skeleton{
  width: 50%;
  height: 30px;
  margin-bottom: 16px;
}

.button-skeleton{
  width: 45%;
  height: 30px;
}

.profile-image-skeleton{
  width: 100%;
  height: 100%;
}

.profile-name-skeleton{
  width: 90px;
  height: 18px;
  margin-bottom: 8px;
}

.profile-mail-skeleton{
  width: 100%;
  height: 15px;
}

.profile-info-skeleton{
  width: 100%;
  height: 300px;
}

.address-skeleton{
  width: 100%;
  height: 100px;
}

.dashboard-card-skeleton{
  width: 100%;
  height: 90px;
}

.dashboard-bar-chart-skeleton{
  width: 100%;
  height: 300px;
  grid-area: e;
}

.dashboard-pie-chart-skeleton{
  width: 100%;
  height: 300px;
  grid-area: f;
}

.price-skeleton{
  width: 50px;
  height: 14px;
}

@keyframes shimmer {
  to{
    background-position: -100% 0;
  }
}

/* --------------------Responsive-------------------- */

@media screen and (max-width: 1200px) {
  .container {
    width: 100%;
  }

  .bestseller-container,
  #browse-results > .container, .wishlist-items {
    grid-template-columns: repeat(3, 1fr);
  }

  .footer-content {
    grid-template-columns: 1fr;
  }

  .footer-content > div {
    padding: 0 !important;
  }
}
@media screen and (max-width: 992px) {
  #trending-category > .container {
    height: 500px;
  }
  .slider > .product-card {
    width: 299px;
  }

  #checkout > .container,
  #order > .container {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 768px) {
  .heading-1 {
    font-size: 28px;
    margin-bottom: 20px;
    line-height: 28px;
  }

  .heading-2 {
    font-size: 20px;
    margin-bottom: 24px;
  }

  .heading-3 {
    font-size: 12px;
  }

  .heading-4{
    font-size: 13px;
  }

  .heading-5{
    font-size: 16px;
    margin-bottom: 40px;
  }

  .body-text-1,
  .body-text-2,
  .body-text-3 {
    font-size: 12px;
  }

  .button-1,
  .button-2 {
    padding: 14px 20px;
    font-size: 12px;
    letter-spacing: 2px;
  }

  .button-3{
    font-size: 10px;
    padding: 2px 4px;
  }

  .brand {
    font-size: 24px;
  }

  .pagination-container > button {
    width: 30px;
    height: 30px;
  }

  .nav-brand {
    margin-right: 14px;
  }

  .navbar-collapse {
    position: absolute;
    top: 60px;
    left: 0;
    padding-inline: 16px;
    width: 100%;
    transform: translateY(0);
    transition: transform 0.3s ease-in-out;
    background-color: var(--white);
    box-shadow: 1px 1px 10px var(--tertiary);
    z-index: -1;
  }

  .collapse {
    transform: translateY(-100%);
  }

  .nav-page-links {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
  }

  .slider > .product-card {
    width: 336px;
  }

  .slider-buttons > button {
    background-color: var(--dark);
    padding: 4px;
    cursor: pointer;
  }
  .slider-buttons > button > svg {
    font-size: 16px;
  }

  .toggle-collapse {
    display: flex;
  }

  #hero{
    min-height: 50vh;
  }

  #hero > .container {
    grid-template-columns: 1fr;
  }

  #hero > .container > div:last-of-type {
    display: none;
  }

  #trending-category > .container {
    height: 400px;
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas:
      "a b "
      "c d ";
  }

  .category > h2 {
    font-size: 18px;
  }

  .bestseller-container,
  #browse-results > .container, .wishlist-items {
    grid-template-columns: repeat(2, 1fr);
  }

  #product-content > .container,
  #promotion-banner > .container {
    grid-template-columns: 1fr;
  }

  .product-content-description {
    padding-inline: 0;
    padding-bottom: 0;
  }

  .product-content-price {
    font-size: 16px;
  }

  .quantity-container > button,
  .quantity-container > span {
    font-size: 14px;
    padding: 4px 10px;
  }

  .quantity-container > span {
    width: 40px;
  }

  .product-reviews{
    grid-template-columns: 1fr;
  }

  .signup-form {
    grid-template-columns: 1fr;
    grid-template-areas:
    'a'
    'b'
    'c'
    'd'
    'e'
    'f'
    'g'
    'h'
    'i';
  }

  .signup-form > label:nth-child(2) {
    margin-left: 0;
  }

  .checkout-cart-item {
    min-height: 100px;
  }

  .checkout-cart-item > p {
    font-size: 12px;
  }

  #profile > .container {
    grid-template-rows: auto;
    grid-template-columns: 1fr;
  }

  .profile-container {
    height: fit-content;
  }

  .update-profile-container,
  .orders-history-container {
    padding-inline: 0;
  }

  .profile-links {
    flex-direction: row;
    height: fit-content;
  }

  .profile-link {
    justify-content: center;
  }
  .profile-link > span {
    display: none;
  }

  .profile-content .heading-3{
    margin-bottom: 32px;
  }

  .dashboard-content{
    grid-template-columns: repeat(2, 1fr);
    grid-template-areas: 
    'a b'
    'c d'
    'e e'
    'f f'
    'g g';
  }

  .order-list-head > span, .product-list-head > span , .user-list-head > span  {
    font-size: 12px;
  }

  .order-item-container > p,  .product-item-container > p, .user-item-container > p{
    font-size: 12px;
  }

  #success > .container > svg, #failed > .container > svg{
    width: 80px;
    height: 80px;
  }

  #failed > .container > h1, #success > .container > h1{
    font-size: 28px;
  }
  
  #success > .container > p, #failed > .container > p{
    font-size: 16px;
  }
  

  .footer-content > div:last-of-type {
    grid-template-columns: repeat(2, 1fr);
  }

  .footer-content > div:last-of-type > ul:nth-child(1),
  .footer-content > div:last-of-type > ul:nth-child(2) {
    margin-top: 48px;
  }
  .footer-content > div:last-of-type > ul:nth-child(3),
  .footer-content > div:last-of-type > ul:nth-child(4) {
    margin-top: 16px;
  }


}
@media screen and (max-width: 480px) {

  .brand {
    font-size: 20px;
  }

  .button-3{
    font-size: 8px;
  }

  .heading-4{
    font-size: 12px;
  }

  .pagination-container > button {
    width: 25px;
    height: 25px;
    font-size: 11px;
  }

  .nav-user-links > li{
    margin-inline: 5px;
  }

  .nav-profile-badge, .nav-user-links > li svg, .toggle-collapse > svg{
    width: 18px;
    height: 18px;
  }

  .slider > .product-card {
    width: 299px;
  }

  .bestseller-container,
  #browse-results > .container, .wishlist-items {
    grid-template-columns: 1fr;
  }

  .form-container {
    width: 100%;
    min-width: 100%;
  }
  .cart-container {
    width: 100%;
  }

  .checkout-cart-head > span {
    font-size: 12px;
  }

  .checkout-item-info > h4,
  .checkout-item-info > h6,
  .checkout-item-info > p {
    font-size: 12px;
  }
  

  .profile-link {
    font-size: 14px;
    padding-inline: 4px;
  }

  .profile-content .heading-3{
    font-size: 16px;
  }

  .update-profile-form {
    display: grid;
    width: 100%;
    grid-template-rows: auto;
    grid-template-columns: 1fr;
    grid-template-areas:
      "a"
      "b"
      "c"
      "d"
      "e"
      "f"
      "g";
  }

  .address-form {
    grid-template-areas: auto;
    grid-template-columns: 1fr;
    grid-template-areas:
      "a"
      "b"
      "c"
      "d"
      "e";
  }

  .heading-5{
    font-size: 14px;
    margin-bottom: 40px;
  }

  .dashboard-card-head > p{
    font-size: 14px;
  }

  #success > .container > svg, #failed > .container > svg{
    width: 60px;
    height: 60px;
  }
  
  #failed > .container > h1, #success > .container > h1{
    font-size: 24px;
  }
  
  #success > .container > p, #failed > .container > p{
    font-size: 14px;
  }
}
